import React from 'react';
import '../index.css';
import flagLogo from '../Assets/flagIcon.png';
import bombLogo from '../Assets/bombIcon.png';


class Square extends React.Component{

    render() {
        let className = "square ";
        let color;
        let shake = "shake";
        if (this.props.value == 0) {
            className += 'empty';
            return (
                <button
                    
                    style={{ color: color }}
                    className={className}
                    onClick={this.props.onClick}>
                    {this.props.value == 0 ? " " : this.props.value}
                </button>
            );
        } 

        if (this.props.value == null) {
            className += 'null';
            return (
                <button
                    
                    style={{ color: color }}
                    className={className}
                    onClick={this.props.onClick}>
                    {this.props.value == 0 ? " " : this.props.value}
                </button>
            );
        } 
        if (this.props.value == 1) {
            className += 'neighborBombNum1';
            color = "red";
            return (
                <button
                    
                    style={{ color: color }}
                    className={className}
                    onClick={this.props.onClick}>
                    {this.props.value == 0 ? " " : this.props.value}
                </button>
            );
        }
        if (this.props.value == 2) {
            className += 'neighborBombNum2';
            color = "green";
            return (
                <button
                    
                    style={{ color: color }}
                    className={className}
                    onClick={this.props.onClick}>
                    {this.props.value == 0 ? " " : this.props.value}
                </button>
            );
        } 
        if (this.props.value == 3) {
            className += 'neighborBombNum3';
            color = "blue";
            return (
                <button
                    
                    style={{ color: color }}
                    className={className}
                    onClick={this.props.onClick}>
                    {this.props.value == 0 ? " " : this.props.value}
                </button>
            );
        } 
        if (this.props.value == 4) {
            className += 'neighborBombNum4';
            color = "black";
            return (
                <button
                    
                    style={{ color: color }}
                    className={className}
                    onClick={this.props.onClick}>
                    {this.props.value == 0 ? " " : this.props.value}
                </button>
            );
        }
        if (this.props.value == 5) {
            className += 'neighborBombNum5';
            color = "orange";
            return (
                <button
                    
                    style={{ color: color }}
                    className={className}
                    onClick={this.props.onClick}>
                    {this.props.value == 0 ? " " : this.props.value}
                </button>
            );
        }
        if (this.props.value == 6) {
            className += 'neighborBombNum6';
            color = "yellow";
            return (
                <button
                    
                    style={{ color: color }}
                    className={className}
                    onClick={this.props.onClick}>
                    {this.props.value == 0 ? " " : this.props.value}
                </button>
            );
        }
        
        if (this.props.value == 'F') {
            className += 'flag';
            return (<button
                className={className}
                onClick={this.props.onClick}>
                <img src={flagLogo} width="32px" height="32px" ></img>
                
            </button>); 
        }
        if (this.props.value == 'B') {
            className += 'bomb';
            return (<button
                className={className}
                onClick={this.props.onClick}>
                <img src={bombLogo} width="30px" height="30px"></img>               

            </button>); 
        }
    };
}

export default Square;