import React from 'react';
import '../index.css';

import Square from './Square.js';

const ROW_SIZE = 16;
const COL_SIZE = ROW_SIZE;

class Board extends React.Component {
    renderSquare(i) {
        let clickedRow = Math.floor(i / ROW_SIZE);
        let clickedCol = i % COL_SIZE;
        return (
            <Square
                value={this.props.seenData[clickedRow][clickedCol]}
                onClick={() => this.props.onClick(i)}
                key={"square" + i}
            />
        );
    }

    render() {
        let squareList = [];
        for (let i = 0; i < ROW_SIZE; i++) {
            let column = [];
            for (let j = 0; j < COL_SIZE; j++) {
                column.push(this.renderSquare(i * ROW_SIZE + j));
            }
            squareList.push(<div className="board-row" key={"row" + i}>{column}</div>);
        }

        return (
            <div>
                {squareList}
            </div>
        );
    }
}

export default Board;